import {Euler} from '../core/Euler';
import {Vector3} from '../core/Vector3';
import {Matrix4} from '../core/Matrix4';
import {Quaternion} from '../core/Quaternion';
import {_Math} from '../helpers/Math';
import {Component} from '../Component';

/**
 * Fired when a new orientation is available
 * @event deviceorientation
 */
const EVT_DEVICE_ORIENTATION = "deviceorientation";

/**
 * Fired when calibration is required
 * @event compassneedscalibration
 */
const EVT_COMPASS_CALIBRATION = "compassneedscalibration";

class Orientation extends Component
{
    /**
     * Uses built-in compass and/or gyroscope to produce orientation Data. Some heplers functions are in there to generate quarternion that can be applied on a camera or a 3D object's transform.
     * 
     * @class Orientation
     * @extends Component
     * @constructor
    */
    constructor(params){
        super(params)

        /**
        * true if the magnetometer/compass is available on the device, false otherwise
        * @property {Boolean} compassAvailable
        */
        this.compassAvailable = window.DeviceOrientationEvent;
    }

    setup(){

        this.compass = new Vector3();

        this.deviceRotationMatrix = new Matrix4();
        this.deviceHeading = null;
        this.deviceAttitude = null;
        this.deviceBank = null;
    }

    on(){
        super.on();

        window.addEventListener("deviceorientation", event => this.onDeviceOrientation(event) );
        window.addEventListener("compassneedscalibration", event => this.onCompassCalibration(event));
    }

    off(){
        super.off();

        window.removeEventListener("deviceorientation", event => this.onDeviceOrientation(event) );
        window.removeEventListener("compassneedscalibration", event => this.onCompassCalibration(event));
    }

    /**
     * Returns the compass/gyro current raw values.
     * This can also be get with the onDeviceOrientation event from the user script
     * 
     * @method getDeviceOrientation
     * @return {Object} compass object with x, y & z components
    */
    getDeviceOrientation()
    {
        return this.compass;       
    }

    /**
    * Creates a Quaternion that matches the device's current orientation. Calculated in radians.
    * @private
    * @method createGyroQuaternion
    * @param {Number} alpha the angle to use for the quaternion creation
    * @param {Number} beta  the angle to use for the quaternion creation
    * @param {Number} gamma the angle to use for the quaternion creation
    * @param {Object} screenOrientation orientation of the screen in degree! Can be very different depending on the plateform...
    * @author Rich Tibbett, https://github.com/richtr/threeVR
    */
    createGyroQuaternion(alpha, beta, gamma, screenOrientation){

        if ( alpha !== 0 && beta !== 0 && gamma !== 0) {

            var finalQuaternion = new Quaternion();
            var deviceEuler = new Euler();
            var screenTransform = new Quaternion();
            var worldTransform = new Quaternion( - Math.sqrt(0.5), 0, 0, Math.sqrt(0.5) ); // - PI/2 around the x-axis
            var minusHalfAngle = 0;

            deviceEuler.set( beta, alpha, -gamma, 'YXZ' );
            finalQuaternion.setFromEuler( deviceEuler );
            minusHalfAngle = -screenOrientation / 2;

            screenTransform.set( 0, Math.sin( minusHalfAngle ), 0, Math.cos( minusHalfAngle ) );

            finalQuaternion.multiply( screenTransform );
            finalQuaternion.multiply( worldTransform );

            return finalQuaternion;
        }else{
            return new Quaternion(0,0,0);//we have nothing to give
        }
    }

    /**
     * Can be used in the transform of a perspective camera to produce a "blind camera" effect : move the screen around to frame a part of the current scene like if you were using a camera.
     * 
     * @method getGyroQuaternion
     */
    getGyroQuaternion()
    {
        if(this.compassAvailable)
        {
            if(this.compass.alpha)
            {
                var quaternion = this.createGyroQuaternion(
                    _Math.degToRad(this.compass.alpha),
                    _Math.degToRad(this.compass.beta),
                    _Math.degToRad(this.compass.gamma),
                    _Math.degToRad(window.orientation || 0)
                );
                return quaternion;
            }
        }
    }

    /**
    * compass calibration event callback
    * @private
    * @method compassCalibrationHandler
    * @param {Object} event
    */
    onCompassCalibration(event)
    {
        //alert("onCompassCalibration call", event);
        this.events.trigger(EVT_COMPASS_CALIBRATION, event);
    }

    /**
    * callback used to access compass event.
    * 
    * @private
    * @method onDeviceorientation
    * @param {Object} event
    */
    onDeviceOrientation(event)
    {
        this.compass.alpha = event.alpha;//x
        this.compass.beta = event.beta;//y
        this.compass.gamma = event.gamma;//z

        //this.compass.heading = event.compassHeading || event.webkitCompassHeading || event.mozCompassHeading || event.ieCompassHeading;// || -event.alpha;

        //When the device doesn't generate a heading information, find a substitute.
        if (event.webkitCompassHeading)
        {
            this.compass.heading = event.webkitCompassHeading;
        }
        else
        {
            this.compass.heading =-event.alpha;
        }

        this.events.trigger(EVT_DEVICE_ORIENTATION, this.compass);
    }

    //FIXME (may be garbage code...)
    computeRotationMatrix( alpha, beta, gamma )
    {

        var _x = beta  ? _Math.degToRad(beta)  : 0; // beta value
        var _y = gamma ? _Math.degToRad(gamma) : 0; // gamma value
        var _z = alpha ? _Math.degToRad(alpha) : 0; // alpha value

        var cX = Math.cos( _x );
        var cY = Math.cos( _y );
        var cZ = Math.cos( _z );
        var sX = Math.sin( _x );
        var sY = Math.sin( _y );
        var sZ = Math.sin( _z );

        //
        // ZXY rotation matrix construction.
        //

        var m11 = cZ * cY - sZ * sX * sY;
        var m12 = - cX * sZ;
        var m13 = cY * sZ * sX + cZ * sY;

        var m21 = cY * sZ + cZ * sX * sY;
        var m22 = cZ * cX;
        var m23 = sZ * sY - cZ * cY * sX;

        var m31 = - cX * sY;
        var m32 = sX;
        var m33 = cX * cY;

        this.deviceRotationMatrix.set(
            m11,    m21,    m31, 0,
            m12,    m22,    m32, 0,
            m13,    m23,    m33, 0,
            0,      0,      0,   1
        );

        if (m12 > 0.998) { // singularity at north pole
            this.deviceHeading = Math.atan2(m31,m33);
            this.deviceAttitude = Math.PI/2;
            this.deviceBank = 0;
            return;
        }
        if (m12 < -0.998) { // singularity at south pole
            this.deviceHeading = Math.atan2(m31,m33);
            this.deviceAttitude = -Math.PI/2;
            this.deviceBank = 0;
            return;
        }

        this.deviceHeading = Math.atan2(-m13,m11);
        this.deviceBank = Math.atan2(-m32,m22);
        this.deviceAttitude = Math.asin(m12);
    }

    updateDeviceRotationMatrix(){
        this.computeRotationMatrix(this.compass.alpha, this.compass.beta, this.compass.gamma);
    }

    getDeviceRotationMatrix() {
        this.updateDeviceRotationMatrix();
        return this.deviceRotationMatrix;
    }
}

// module exports
export { Orientation };
