import {Mesh} from '../shape/Mesh';
import {Material} from '../scene/Material';
import {Color} from '../core/Color';
import {Vector2} from '../core/Vector2';
import {Vector3} from '../core/Vector3';
import {debug} from '../helpers/Debug';


/**
 * OBJ class gives easy access to some calculations specific to OBJs
 *
 * @class OBJ
 */

class OBJ
{

    /**
     * Load an OBJ and dependencies from a URL
     * @method load
     * @static
     * @param {Loader} loader
     * @param {Object} params The parameters object
     * @param {Function} [params.onStart = null] A function to be called when the loading starts
     */
    
    
    static load(loader, params)
    {
        OBJ.callBack = params.onLoad;
        return loader.loadText({
                               url:params.url,
                               onLoad:OBJ.parseOBJ.bind(null,loader)
                        });
    }
    
  
    static attachTexture(loader, model, data)
    {
        console.log("attachTexture ", loader, model, data);
        model.material.setTexture(data);
    }
    
    
    static parseMTL(loader, model, data)
    {
        console.log("model=" + model);
        //OBJ.mtl = data;
        console.log("mtl content =", data);
        var lines = data.toString().split("\n");
        //console.log("MTL lines = " + lines);
        lines.forEach(line => {
                      var words = line.split(" ");
                      //console.log("WORDS:"+words);
                      switch(words[0])
                      {
                      //comment, we just ignore
                      case "#":
                      break;
                      //new material declared
                      case "newmtl":
                      console.log("new material : " + words[1]);
                      break;
                      //ambient term
                      case "Ka":
                      model.material.setEmissiveColor(new Mobilizing.Color(words[1],words[2],words[3]));
                      break;
                      case "Kd": //diffuse
                      model.material.setColor(new Mobilizing.Color(words[1],words[2],words[3]));
                      break;
                      
                      case "Ks": //specular
                      model.material.setSpecularColor(new Mobilizing.Color(words[1],words[2],words[3]));
                      model.material._material.shininess = 20;
                      break;
                      
                      case "Ns": //specular exponent
                      
                      break;
                      
                      case "d": //dissolved == opacity
                      
                      break;
                      
                      case "Tr": //Transparent
                      //d = 1-Tr;
                      break;
                      
                      case "illum": //illumination model
                      
                      break;
                      
                      case "map_Kd": //Map diffuse
                      //load texture
                      //console.log("load texture : " + words[1]);
                      var texture = loader.loadTexture({url:words[1], onLoad:OBJ.attachTexture.bind(null,loader, model)});
                      model.updateMaterial();
                      
                      break;
                      default:
                      console.log("unknown MTL line : " , words);
                      break;
                      }
                      });

                    //model.material.setColor(Mobilizing.Color.green);
        
    }
    
    static parseOBJ(loader, data)
    {
        console.log("onLoaded data=", data);
        console.log("loader=", loader);
        //console.log("onLoaded result=", this.result);
        //this.text = data.getResponseText();
        OBJ.text = data;
        console.log("text=", OBJ.text);
        //this.text = this.result._value; //FIXME
        
        var p;
        if (OBJ.mesh !== undefined)
        {
            p = OBJ.mesh;
        }
        else
        {
            console.log("create material");
            p = new Mesh({});
            /*if(OBJ.materialType !== null){
                p.material = new Material({type:OBJ.materialType});
            }else{
                p.material = new Material({type:"phong"});
            }
             */
            p.material = new Material({type:"phong"});
            console.log("Material created : " , p.material);
            //p.material.setColor(Mobilizing.Color.red);
            //p.material.setTexture(texture);
            //p.updateMaterial();
            //p.material.color = Color.white;
            //p.material.setWireframe(true);
        }
        
        console.log("parsing obj file...");
        //OBJ parsing =====================================
        var lines = OBJ.text.toString().split("\n");
        var uvs = [];
        var normals = [];
        lines.forEach(line => {
                      var words = line.split(" ");
                      
                      //debug.log("LINE " + line);
                      switch(words[0])
                      {
                      case "mtllib": //chargement de la lib
                      console.log("mtllib " + words[1]);
                      OBJ.resultmtl = loader.loadText({
                                      url:words[1],
                                      onLoad:OBJ.parseMTL.bind(null,loader, p)
                                      });
                      break;
                      
                      case "usemtl":
                      console.log("usemtl " + words[1]);
                      //to do : load mtl file

                      break;
                      
                      case "o":
                      console.log("OBJ o " + words[1]);
                      break;
                      
                      case "g":
                      console.log("OBJ g " + words[1]);
                      break;
                      
                      case "v":
                      //debug.log("Vertice")
                      var v = new Vector3(words[1], words[2], words[3]);
                      p.pushVertex(v);
                      break;
                      
                      case "vt":
                      debug.log("uv " + words[1] + " " + words[2]);
                      var vUV = new Vector2(words[1], words[2]);
                      uvs.push(vUV);
                      break;
                      
                      case "vn":
                      var vN = new Vector3(words[1], words[2], words[3]);
                      normals.push(vN);
                      //debug.log("normal")
                      break;
                      
                      case "s":
                      //debug.log("Face")
                      break;
                      
                      case "f": //FACE definition
                      //debug.log(uvs);
                      //debug.log("Face")
                      var index1 = words[1].split("/");
                      var index2 = words[2].split("/");
                      var index3 = words[3].split("/");
                      p.pushFace(index1[0]-1, index2[0]-1, index3[0]-1);
                      p.pushUV(uvs[index1[1]-1], uvs[index2[1]-1], uvs[index3[1]-1]);
                      p.pushNormal(normals[index1[2]-1], normals[index2[2]-1], normals[index3[2]-1]); //NEW
                      
                      if (words.length > 4)
                      {
                      //we have quads, we need to add an additionnal triangle
                      var index4 = words[4].split("/");
                      p.pushFace(index1[0]-1, index3[0]-1, index4[0]-1);
                      p.pushUV(uvs[index1[1]-1], uvs[index3[1]-1], uvs[index4[1]-1]);
                      }
                      break;
                      
                      case "#":
                      //debug.log("Comment")
                      break;
                      }
                      });
        
        p.computeNormals();
        //p.updateMaterial();
        console.log("callback called : ", p);
        OBJ.callBack(p);
        //p.material.setTexture(OBJ.texture);
        
    }
}

// module exports
export { OBJ };
